/*
// 密度管理図自体の要望がどうしても生じるので、作図できるようにする。
*/
import React from 'react';
// import _ from 'underscore';
import SDMDiagram from './sdm-core';
//
//
import { scaleLog, scaleOrdinal } from '@visx/scale';
import { AxisLeft, AxisBottom } from '@visx/axis';
import { GridRows, GridColumns } from '@visx/grid';
import { Group } from '@visx/group';
import { curveBasis, curveLinear, curveNatural, curveCardinal } from '@visx/curve';
import { LinePath } from '@visx/shape';
import { GlyphStar } from '@visx/glyph';
import { Text } from '@visx/text';
import { LegendOrdinal } from '@visx/legend';
//
import { Card, CardContent } from '@mui/material';


function DrawSDMDiagram(id, plantingInput, dv) {
  // x軸範囲設定
  const xTop = (plantingInput <= 5000 ? 5000 : 12000);
  const xBottom = 100;
  const xTickValue = [500, 1000, 2000, 3000, 4000, 5000];
  const xTickValueEx = [500, 1000, 5000, 10000];

  // 植栽本数配列
  const planting = new Array(24).fill(null).map((_, i) => (i + 1) * 500);
  // 樹高配列
  const height = new Array(41).fill(null).map((_, i) => (i + 2));
  // 個体密度配列
  const density = new Array(41).fill(null).map((_, i) => (xBottom + i * 200));
  // 収量比数配列
  const ry = [1, 0.95, 0.9, 0.8, 0.7, 0.6, 0.5];

  // 密度管理コアオブジェクト作成
  let localDC = new SDMDiagram(id);

  // y軸範囲設定
  const yTop = Math.ceil(localDC.volumeMaxD2(localDC.densityHMaxD(Math.max(...height)))/1000)*1000;  // スプレッド構文 (...) の利用で最大値取得
  const bottomTmp = localDC.volumeHD2(10, xBottom);
  const yBottom = bottomTmp > 10 ? Math.floor(bottomTmp/10)*10 : 10;
  const yLabel = '林分材積（ m^3 ／ ha）';

  // scales (対数軸設定)
  const densityScale = scaleLog({
    domain: [xBottom, xTop],
    clamp: true,
  });
  const volumeScale = scaleLog({
    domain: [yBottom, yTop],
    clamp: true,
  });
  // graph margin
  const defaultMargin = { top: 30, right: 30, bottom: 70, left: 70 };

  // 自然枯死曲線描画用データ
  const nThinning = planting
    .map(
      (x) => (height
        .flatMap(
          (y) => {
            const dv = localDC.PH2DV2(y, x);
            return (
              dv.density >= xBottom && dv.density <= xTop && dv.volume >= yBottom && dv.volume <= yTop
                ? dv
                : []
            )
          }
        )
      )
    );

  // 等樹高曲線データ
  let evenHeight = height.filter(h => h % 5 === 0)
    .map(
      (h) => {
        const densityPlus = density.concat([localDC.densityHMaxD(h)]);
        return (
          densityPlus.flatMap(
            (d) => {
              const MaxD = localDC.densityHMaxD(h);
              const vol = localDC.volumeHD2(h, d)
              return (
                d <= MaxD && d <= xTop && d >= xBottom && vol >= yBottom && vol <= yTop
                  ? { density: d, volume: vol }
                  : []
              )
            }
          )
        )
      }
    );
    // 等樹高曲線ラベルデータ
    const labelEvenHeight = evenHeight.filter(d => d.length > 0)
    .map( (d, i) => {
      return(
        {height: (i+1)*5, density: d[0].density, volume: d[0].volume,}
        )
    });

  // 収量比数線データ
  // height における最多密度とその際の林分材積
  const dvMaxD = height.map((h) => {
    const outDensity = localDC.densityHMaxD(h);
    return ({ density: outDensity, volume: localDC.volumeMaxD2(outDensity) });
  });
  // height における各収量比数時の林分材積と個体密度（flatMapで枠外データ除外）
  const ryTarget = ry.map(r => (
    dvMaxD.flatMap((dv, i) => {
      const vol = r * dv.volume;
      const den = localDC.densityHV2((i + 2), vol);
      return (
        vol <= yTop && vol > yBottom && den > xBottom && den <= xTop
          ? { density: den, volume: vol }
          : []
      );
    })
  ));
  // 収量比数線ラベルデータ
  const labelRy = ryTarget.filter(d => d.length > 0)
    .map((d, i) => {
      return (
        { ry: ry[i], density: d[d.length-1].density, volume: d[d.length-1].volume, }
      )
    });

  // 凡例データ
  const legendScale = scaleOrdinal({
    domain: ["自然枯死曲線", "収量比数線", "等樹高曲線"],
    range: ["#2A2", "#A33", "#22A"],
  });



  // グラフ描画設定
  const svgWidth = 640;
  const svgHeight = 480;
  const background = '#f8f8f8'; // うっすら灰色
  const margin = defaultMargin;
  const xMax = svgWidth - margin.left - margin.right;
  const yMax = svgHeight - margin.top - margin.bottom;
  densityScale.range([0, xMax]);
  volumeScale.range([yMax, 0]);

  return (
    nThinning != null ?
      <div>
        <svg width={svgWidth} height={svgHeight}>
          {/* グラフ描画ボックス */}
          <rect x={0} y={0} width={svgWidth} height={svgHeight} fill={background} rx={14} />
          <Group left={margin.left} top={margin.top}>

            {/* 軸描画 */}
            <GridRows scale={volumeScale} width={xMax} height={yMax} stroke="#e0e0e0" />
            <GridColumns scale={densityScale} width={xMax} height={yMax} stroke="#e0e0e0" />
            <line x1={xMax} x2={xMax} y1={0} y2={yMax} stroke="#e0e0e0" />
            <AxisBottom
              top={yMax}
              scale={densityScale}
              tickValues={plantingInput <= 5000 ? xTickValue : xTickValueEx}
              tickFormat= {(v) => {return `${v}`} }
              label={"個体密度 (本／ha)"}
              labelProps={{fontSize: 16, textAnchor: "middle",}}
            />
            <AxisLeft
              scale={volumeScale}
              numTicks = {5}
              tickValues={[20, 50, 100, 200, 500, 1000, 2000]}
              tickFormat= {(v) => {return `${v}`} }
              label= {`${yLabel}` }
              labelProps={{fontSize: 16, textAnchor: "middle",}}
            />

            {/* 自然枯死曲線描画 */}
            {nThinning.map((data, index) => {
              return (
                <LinePath
                  data={data}
                  curve={curveBasis}
                  x={(d) => densityScale(d.density) ?? 0}
                  y={(d) => volumeScale(d.volume) ?? 0}
                  stroke="#2A2"
                  strokeWidth={1.2}
                  strokeOpacity={0.8}
                />
              )
            })}
            
            {/* 等樹高曲線描画 */}
            {evenHeight.map((data, index) => {
              return (
                <LinePath
                  data={data}
                  curve={curveCardinal}
                  x={(d) => densityScale(d.density) ?? 0}
                  y={(d) => volumeScale(d.volume) ?? 0}
                  stroke="#22A"
                  strokeWidth={1}
                  strokeOpacity={0.8}
                />
              )
            })}
            {/* 等樹高曲線ラベル */}
            {labelEvenHeight.filter(d => d.height % 10 === 0)
            .map((data) =>{
              return (
                <Text
                  x={densityScale(data.density*1.1)}
                  y={volumeScale(data.volume*1.1)}
                  fill = "#22A"
                  verticalAnchor="start"
                  fontSize = "0.8em"
                >
                  {`${data.height}m`}
                </Text>
              );
            })}

            {/* 収量比数線描画 */}
            {ryTarget.map((data, index) => {
              return (
                index === 1 ?
                  /* Ry =0.95 は破線にする */
                  <LinePath
                    data={data}
                    curve={curveNatural}
                    x={(d) => densityScale(d.density) ?? 0}
                    y={(d) => volumeScale(d.volume) ?? 0}
                    stroke="#A33"
                    strokeWidth={1}
                    strokeDasharray={[9, 5]}  // 破線の設定
                    strokeOpacity={0.8}
                  />
                  /* それ以外は実線に */
                  : <LinePath
                    data={data}
                    curve={curveNatural}
                    x={(d) => densityScale(d.density) ?? 0}
                    y={(d) => volumeScale(d.volume) ?? 0}
                    stroke="#A33"
                    strokeWidth={1}
                    strokeOpacity={0.8}
                  />
              )
            })}
            {/* 収量比数線ラベル */}
            {labelRy.filter(d => (d.ry*10+1) % 2 === 0)
            .map((data) =>{
              return (
                <Text
                  x={densityScale(data.density)}
                  y={volumeScale(data.volume)}
                  fill = "#A33"
                  textAnchor="middle"
                  horizontalAnchor="end"
                  fontSize = "0.8em"
                >
                  {`Ry=${data.ry}`}
                </Text>
              );
            })}
          </Group>
          <Group left={margin.left} top={margin.top}>
            <GlyphStar
              left={densityScale(dv.density) ?? 0}
              top={volumeScale(dv.volume) ?? 0}
              size={110}
              fill="#FFA"
              stroke="#AA3"
              strokeWidth={2}
            />
          </Group>

        </svg>
              {/* 凡例 */}
      <Card
        variant="outlined"
        sx={{
          width: 140,
          height: 60,
          position: 'relative',
          top: -475,
          left: 510,
        }}
      >
        <CardContent
          sx={{
            fontSize: 12,
            align: "center",
            lineHeight: 1.5,
            ml: 1,
            mr: 1,
            mt: 0,
            p: 0,
          }}
        >
          <LegendOrdinal
            scale={legendScale}
            direction="column"
            itemDirection="row"
            shape="line"
            labelMargin="0 15px 0 0"
          />
        </CardContent>
      </Card>

      </div>
      : ""
  );
};

export default DrawSDMDiagram;

