// 密度管理図関連の計算式クラス
// 基本パラメータは、parameters.json で管理
import brentsMethod from 'brents-method';
import minimize from 'minimize-golden-section-1d';
//
const parameters = require('./parameters.json');

class SDMDiagram {
  constructor(id) {
    // まずパラメータを取得可能かどうかチェック。
    this.id = id != null ? id : 0;

    let p = parameters[id];
    let k1 = p.b4 / (p.b2 - p.b4);
    const setK2 = (k1) => { return p.Rf / p.b3 * (p.Rf / (1 - p.Rf) * p.b1 / p.b3) ** k1 };
    const setK5 = (k1) => { return k1 * p.Rf / p.b3 * (p.Rf / (1 - p.Rf) * p.b1 / p.b3) ** k1 * (p.b2 / p.b4) ** (k1 + 1) };
    // これ以降、パラメータ代入
    this.p = p;
    this.k1 = k1;
    this.k2 = setK2(k1);
    this.k5 = setK5(k1);
    this.state = {
      p: p,
      k1: k1,
      k2: this.k2,
      k5: this.k5,
    };
  };

  // パラメータ再設定
  setParms = async (inputID) => {
    let localID = inputID == null ? this.id : inputID;
    let lp = parameters[localID];
    let lk1 = lp.b4 / (lp.b2 - lp.b4);

    const setK2l = (k1) => { return lp.Rf / lp.b3 * (lp.Rf / (1 - lp.Rf) * lp.b1 / lp.b3) ** k1 };
    const setK5l = (k1) => { return k1 * lp.Rf / lp.b3 * (lp.Rf / (1 - lp.Rf) * lp.b1 / lp.b3) ** k1 * (lp.b2 / lp.b4) ** (k1 + 1) };

    this.id = localID;
    this.p = lp;
    this.k1 = lk1;
    this.k2 = setK2l(lk1);
    this.k5 = setK5l(lk1);
    this.state = {
      p: lp,
      k1: lk1,
      k2: this.k2,
      k5: this.k5,
    };

    return 0;
  }

  // 最大到達密度（植栽本数から自然枯死のみで最多密度に達したときの個体数）
  maximumDensity(planting) {
    return (this.k1 + 1) / this.k1 * planting;
  }

  // 自然枯死曲線（個体材積版。初期密度、平均個体材積から個体密度推定）
  naturalThinning1(planting, volumeI) {
    return (1 / planting - volumeI / (this.k5 * planting ** (this.k1 + 1))) ** (-1)
  };

  // 自然枯死曲線（林分材積版。初期密度、林分材積から個体密度推定）
  naturalThinning2(planting, volumeA) {
    return (planting + volumeA / (this.k5 * planting ** (this.k1)))
  };

  // 最多密度曲線（個体材積版。 最多密度となる場合の平均個体材積を推定）
  volumeMaxD1(density) {
    return this.k2 * density ** this.k1;
  };

  // 最多密度曲線（林分材積版。 最多密度となる場合の林分材積を推定）
  volumeMaxD2(density) {
    return this.k2 * density ** (this.k1 + 1);
  };

  // 最多密度（個体材積版。与えられた樹高時の最多密度）
  densityHMaxD(height) {
    return (1 - this.p.Rf) / this.p.Rf * this.p.b3 / this.p.b1 * height ** (this.p.b4 - this.p.b2);
  }

  // 最多密度（林分材積版。与えられた林分材積に相当する個体密度（volumeMaxD2 の逆数））
  densityV2MaxD(volumeA) {
    return (volumeA / (this.k2)) ** (1 / (this.k1 + 1));
  }

  // 等平均樹高曲線（個体材積版。樹高・立木密度から平均個体材積を推定）
  volumeHD1(height, density) {
    const dMax = this.densityHMaxD(height);
    let density2 = Math.min(density, dMax);
    return (density2 * this.p.b1 * height ** this.p.b2 + this.p.b3 * height ** this.p.b4) ** (-1);
  };

  // 等平均樹高曲線（林分材積版。樹高・立木密度から林分材積を推定）
  volumeHD2(height, density) {
    // 最多密度以上にならないように個体密度を調整
    const dMax = this.densityHMaxD(height);
    let density2 = Math.min(density, dMax);
    return (this.p.b1 * height ** this.p.b2 + (1 / density2) * this.p.b3 * height ** this.p.b4) ** (-1);
  };

  // 樹高・林分材積から個体密度推定（等平均樹高曲線・個体材積版の逆関数）
  densityHV1(height, volumeI) {
    return ((1 / volumeI - this.p.b3 * height ** this.p.b4) * 1 / (this.p.b1 * height ** this.p.b2));
  }

  // 樹高・林分材積から個体密度推定（等平均樹高曲線・林分材積版の逆関数）
  densityHV2(height, volumeA) {
    return ((volumeA * this.p.b3 * height ** this.p.b4) / (1 - volumeA * this.p.b1 * height ** this.p.b2));
  }

  // 目標樹高材積（林分材積版。初期密度と目標樹高から林分材積、個体密度を推定）
  PH2DV2(height, planting) {
    // ブレンツ法で目標樹高時の個体密度を求解
    let resultD = brentsMethod(
      x => this.volumeHD2(height, x) - (x - planting) * (this.k5 * planting ** (this.k1)),
      0,
      planting,
      { errorTolerance: 1e-4, maxIterations: 100 },
    );
    let resultV = this.volumeHD2(height, resultD);
    const altDensity = this.densityHMaxD(height);
    const altVolume = this.volumeMaxD2(altDensity);
    const dMax = this.maximumDensity(planting);

    // 個体数が最多密度より少なくなっていた場合、最多密度とする。
    if (resultD < dMax) {
      resultD = altDensity;
      resultV = altVolume;
    };

    // 出力
    return { 'density': resultD, 'volume': resultV };
  };

  // 植栽本数推定（林分材積版。目標樹高到達時の個体密度から植栽本数を推定（間伐影響評価用））
  DV2Planting2(density, volumeA) {
    // ブレンツ法で目標樹高時の個体密度を求解
    return brentsMethod(
      x => density - (x + volumeA / (this.k5 * x ** (this.k1))),
      0,
      density,
      { errorTolerance: 1e-2, maxIterations: 100 },
    );
  }

  // 樹高、材積から植栽本数を推定（林分材積版。）
  HV2Planting2(height, volumeA) {
    // ブレンツ法で目標樹高時の個体密度を求解
    return brentsMethod(
      x => x + volumeA / (this.k5 * x ** (this.k1)) - (this.p.b3 * height ** this.p.b4) / (1 / volumeA - this.p.b1 * height ** this.p.b2),
      0,
      5000,  // caution!! 植栽本数の上限を設定しています。
      { errorTolerance: 1e-2, maxIterations: 100 },
    );
  }

  // 与えられた樹高における最適植栽本数（＝収益最大化）
  OptimalPlanting(height, revenue, priceSapling, costManagement) {
    const func = (planting) => {
      const volumeHat = this.PH2DV2(height, planting).volume;
      return (-(volumeHat * revenue - (costManagement + planting * priceSapling)));
    };

    // 数値最適化
    let solution = minimize(func,  {tolerance: 1e-3, lowerBound: 0, upperBound: 5000});
    return (solution);
  }
};

export default SDMDiagram;