// 密度管理図概要カード
import Typography from '@mui/material/Typography';
import Box from '@mui/system/Box';
import Card from '@mui/material/Card';
import CardHeader from '@mui/material/CardHeader';
import CardContent from '@mui/material/CardContent';


const CalcSDMSummary = (sdm, height, planting) => {
  const outMaxD = sdm.densityHMaxD(height);
  const outMaxV = sdm.volumeMaxD2(outMaxD)
  const outDV = sdm.PH2DV2(height, planting);
  const outRy = outDV.volume / outMaxV;
  return (
    <Card
      id="DC-summary"
      sx={{
        width: 460,
        boxShadow: 3,
      }}
    >
      <CardHeader title="密度管理図概要"
        sx={{
          backgroundColor: '',
          p: 1,
          ml: 0,
          height: { xs: 1 },
        }}
      />
      <CardContent
        sx={{
          m: 0,
        }}
      >
        <Typography l={1} id="result DC" component="div">
          <Box textAlign="left" m={1.5}>
            ・樹高{height}m時の最多密度:
            <font size="+1"> {Math.round(outMaxD)} </font> 本ha<sup>-1</sup>
          </Box>
          <Box textAlign="left" m={1.5}>
            ・樹高{height}m時の最多林分材積:
            <font size="+1"> {Math.round(outMaxV)} </font> m<sup>3</sup>ha<sup>-1</sup>
          </Box>
          <Box textAlign="left" m={1.5}>
            ・{planting}本植栽で樹高{height}mの材積:
            <font size="+1"> {Math.round(outDV.volume)} </font> m<sup>3</sup>ha<sup>-1</sup>
          </Box>
          <Box textAlign="left" m={1.5}>
            ・{planting}本植栽で樹高{height}mの個体密度:
            <font size="+1"> {Math.round(outDV.density)} </font> 本 ha<sup>-1</sup>
          </Box>
          <Box textAlign="left" m={1.5}>
            ・樹高{height}m時の収量比数:
            <font size="+1" color={outRy > 0.95 ? "red" : "black"}> {Math.round(outRy * 100) / 100} </font>
          </Box>
        </Typography>
      </CardContent>
    </Card>
  );
};

export default CalcSDMSummary;
