//
import * as React from 'react';
import PropTypes from 'prop-types';
import { useSelector } from 'react-redux';
//
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import CssBaseline from '@mui/material/CssBaseline';
import Divider from '@mui/material/Divider';
import Drawer from '@mui/material/Drawer';
import IconButton from '@mui/material/IconButton';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import MenuIcon from '@mui/icons-material/Menu';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
//
import SliderHeight from './slider-height';
import SliderPlanting from './slider-planting';
import SliderPriceSapling from './slider-sapling_price';
import SliderRevenue from './slider-revenue';
import SliderCostManagement from './slider-management_cost';
import SliderMAI from './slider-MAI';
import SelectorSp from './selector-sp';
import SelectorID from './selector-id';
import SelectorDR from './selector-DR';
import SelectorPY from './selector-PY';
//
import CalcSDMSummary from './card-sdm';
import DrawSDMDiagram from './sdm-diagram';
import CalcLEVSummary from './card-lev';
import OptimalPlanting from './optimal-planting';

// ドロワーのサイズ
const drawerWidth = 540;

function App(props) {

  // 
  const { window } = props;

  // 
  const [mobileOpen, setMobileOpen] = React.useState(false);

  // ドロワー開閉のハンドラ
  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  // store => ローカルデータ
  const localID = useSelector(state => state.id);
  const localSDM = useSelector(state => state.sdm);
  const localDV = useSelector(state => state.dv);
  const localHeight = useSelector(state => state.height);
  const localPlanting = useSelector(state => state.planting);
  const localOptP = useSelector(state => state.optPlanting);
  const localOptV = useSelector(state => state.optVolume);
  const localLEV = useSelector(state => state.lev);
  const localPriceS = useSelector(state => state.priceSapling);
  const localCostM = useSelector(state => state.costManagement);
  const localMAI = useSelector(state => state.mai)

  //ドロワーの内容。 hook 利用のため、別モジュール化はできないみたい
  const drawer = (
    <div>
      <Toolbar />
      <Divider />
      {SelectorSp()} {SelectorID()}
      <Divider sx={{mb:{xs: 2},}}/>
        <Box sx={{p:{xs: 1}, px:{xs: 3},}}> {SliderHeight()} </Box>
        <Box sx={{p:{xs: 1}, px:{xs: 3},}}> {SliderPlanting()}  </Box>
      <Divider sx={{mb:{xs: 2},}}/>
      <Box sx={{p:{xs: 1}, px:{xs: 3},}}> {SliderRevenue()} </Box>
      <Box sx={{p:{xs: 1}, px:{xs: 3},}}> {SliderPriceSapling()} </Box>
      <Box sx={{p:{xs: 1}, px:{xs: 3},}}> {SliderCostManagement()} </Box>
      <Box sx={{p:{xs: 1}, px:{xs: 3},}}> {SliderMAI()} </Box>
      <List>
        <ListItem sx={{mx: "auto",width: drawerWidth*0.9,}}> {SelectorDR()} {SelectorPY()} </ListItem>
      </List>
    </div>
  )

  // ウィンドウサイズ判定スイッチ
  const container = window !== undefined ? () => window().document.body : undefined;

  // 出力本体
  return (
    <>
      <Box sx={{ display: 'flex' }}>
        <CssBaseline />
        <AppBar
          position="fixed"
          sx={{
            width: { sm: `calc(100% - ${drawerWidth}px)` },
            ml: { sm: `${drawerWidth}px` },
          }}
        >
          <Toolbar>
            <IconButton
              color="inherit"
              aria-label="open drawer"
              edge="start"
              onClick={handleDrawerToggle}
              sx={{ mr: 2, display: { sm: 'none' } }}
            >
              <MenuIcon />
            </IconButton>
            <Typography variant="h6" noWrap component="div">
              LEV on Stand Density Management
            </Typography>
          </Toolbar>
        </AppBar>

        {/* ドロワー */}
        <Box
          component="nav"
          sx={{ width: { sm: drawerWidth }, flexShrink: { sm: 0 } }}
          aria-label="mailbox folders"
        >
          {/* The implementation can be swapped with js to avoid SEO duplication of links. */}
          {/* 折り畳みドロワー（小画面用） */}
          <Drawer
            container={container}
            variant="temporary"
            open={mobileOpen}
            onClose={handleDrawerToggle}
            ModalProps={{
              keepMounted: true, // Better open performance on mobile.
            }}
            sx={{
              display: { xs: 'block', sm: 'none' },
              '& .MuiDrawer-paper': { boxSizing: 'border-box', width: drawerWidth },
            }}
          >
            {drawer}
          </Drawer>

          {/* 表示ドロワー（大画面用） */}
          <Drawer
            variant="permanent"
            sx={{
              display: { xs: 'none', sm: 'block' },
              '& .MuiDrawer-paper': { boxSizing: 'border-box', width: drawerWidth },
            }}
            open
          >
            {drawer}
          </Drawer>
        </Box>

        {/* メイン画面 */}
        <Box
          component="main"
          sx={{
            flexGrow: 1,
            p: 3,
            width: { sm: `calc(100% - ${drawerWidth}px)` },
          }}
        >
          <Toolbar />
            {localID !== "" ? (
            <>
              <Box sx={{ minWidth: 720, }}>
                {DrawSDMDiagram(localID, localPlanting, localDV)}
              </Box>
              <Box>
                {CalcSDMSummary(localSDM, localHeight, localPlanting)}
              </Box>
              <Box sx={{ ｗidth: 720, }}>
              {OptimalPlanting(localSDM, localLEV, localHeight, localPriceS, localCostM, localOptP, localOptV)}
              </Box>
              <Box>
                {CalcLEVSummary(localSDM, localLEV, localOptP, localOptV, localMAI, localPriceS, localCostM)}
              </Box>
            </>
            )
            : ""}
        </Box>
      </Box>
    </>
  );
}

App.propTypes = {
  /**
   * Injected by the documentation to work in an iframe.
   * You won't need it on your project.
   */
  window: PropTypes.func,
};

export default App;
