//
// stage-2 用の 製材歩留セレクタコンポーネント
//

import React from 'react';
import { useSelector, useDispatch } from 'react-redux';

// Material-UI selector （var 5）
import Box from '@mui/material/Box';
import InputLabel from '@mui/material/InputLabel';
import OutlinedInput from '@mui/material/OutlinedInput';
import { styled } from '@mui/styles';
import Tooltip, { tooltipClasses } from '@mui/material/Tooltip';
//

// サイズ指定可能なtool-tipボックス
const WideTooltip = styled(({ className, ...props }) => (
  <Tooltip {...props} classes={{ popper: className }} />
))({
  [`& .${tooltipClasses.tooltip}`]: {
    maxWidth: 300,
  },
});

// コンポーネント本体
function SelectorPY() {
  // 各種hook設定
  const dispatch = useDispatch();
  const processYield = useSelector(state => state.processYield);
  const setProcessYield = (newPY) => {
    dispatch({ type: 'SET_PROCESS_YIELD', payload: newPY });
  }

  // 歩留まり調整
  const textPYInput = `
    LEVによる収益判断のための丸太生産歩留まりを設定。
    `;
  const handleProcessYieldInput = (event) => {
    setProcessYield(event.target.value === '' ? NaN : Number(event.target.value));
  };
  const handleProcessYieldBlur = () => {
    if (processYield < 0.1) {
      setProcessYield(0.1);
    } else if (processYield > 1.0) {
      setProcessYield(1.0);
    }
  };


  // 出力
  return (
    <Box sx={{mx:{xs:6},}}>
      {/*製材歩留選択*/}
      <InputLabel htmlFor="outlined-adornment-amount">歩留</InputLabel>
      <WideTooltip title={textPYInput} placement="top-start" arrow>
        <OutlinedInput
          sx={{
            backgroundColor: "#ffffff",
            width: 108,
          }}
          value={processYield}
          onChange={handleProcessYieldInput}
          onBlur={handleProcessYieldBlur}
          inputProps={{
            step: 0.1,
            min: 0.1,
            max: 1.0,
            type: 'number',
            'aria-labelledby': 'processYield',
          }}
        />
      </WideTooltip>
    </Box>
  );
}

export default SelectorPY;
