
import React , {useState} from 'react';
import {useSelector, useDispatch} from 'react-redux';

// Material-UI selector （var 5）
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import Slider from '@mui/material/Slider';
import Input from '@mui/material/Input';
import InputAdornment from '@mui/material/InputAdornment';
import { styled } from '@mui/styles';
import Tooltip, { tooltipClasses } from '@mui/material/Tooltip';
//

// サイズ指定可能なtool-tipボックス
const WideTooltip = styled(({ className, ...props }) => (
    <Tooltip {...props} classes={{ popper: className }} />
  ))({
    [`& .${tooltipClasses.tooltip}`]: {
      maxWidth: 300,
    },
  });

// コンポーネント本体
function SliderRevenue() {
  // 各種hook設定
  const dispatch = useDispatch();
  const revenue = useSelector(state => state.revenue);
  const setRevenue = (newRevenue) =>{
    dispatch({type: 'SET_REVENUE', payload: newRevenue});
  }
  const [balance, setBalance] = useState([6000,10000]);  // ローカルで利用する収支

  // 材収益調整
  const textRevenueSlider = `
    伐出純益を設定。スライダーの右端で売り上げ額を、左端で伐出経費額を設定できます。
    `;
  const handleRevenueSlider = (event, newBalance) => {
    setBalance(newBalance);  // ローカルでバランスを利用
    const newRevenue = Math.abs(newBalance[0] - newBalance[1])
    setRevenue(newRevenue);
  };
  const handleRevenueInput = (event) => {
    setRevenue(event.target.value === '' ? NaN : Number(event.target.value));
    const newBalance = [balance[1] - Number(event.target.value), balance[1]];
    setBalance(newBalance);
  };
  const handleRevenueBlur = () => {
    if (revenue < 3000) {
      setRevenue(3000);
    } else if (revenue > 12000) {
      setRevenue(12000);
    }
  };
  const rMarks = [
    { value:    0, label: '0', },
    { value:  500, label: '', },
    { value: 1000, label: '1,000', },
    { value: 2000, label: '', },
    { value: 3000, label: '', },
    { value: 4000, label: '', },
    { value: 5000, label: '5,000', },
    { value: 6000, label: '', },
    { value: 7000, label: '', },
    { value: 8000, label: '', },
    { value: 9000, label: '', },
    { value: 10000, label: '10,000円', },
    { value: 11000, label: '', },
    { value: 12000, label: '', },
  ];

  // 出力
  return (
    <>
                {/*伐出純益スライダ*/}
                <Typography id="revenue-input-title">
                  伐出純益
                </Typography>
                <WideTooltip title={textRevenueSlider} placement="top-start" arrow>
                  <Grid container id="revenue-inputs" spacing={4} alignItems="center"
                    sx={{
                      mb: { xs: 3 },
                    }}
                  >
                    <Grid item id="revenue-slider" xs>
                      <Slider
                        // value={typeof balance === 'number' ? balance : [1000,5000]}
                        value={balance}
                        onChange={handleRevenueSlider}
                        getAriaValueText={(v) => { return ('{$v}円') }}
                        aria-labelledby="revenue-slider"
                        step={500}
                        marks={rMarks}
                        min={0}
                        max={12000}
                        valueLabelDisplay="auto"
                      />
                    </Grid>
                    <Grid item id="revenue-number" >
                      <Input
                        sx={{
                          backgroundColor: "#ffffff",
                          ml: { xs: 1 },
                          width: 120, 
                        }}
                        value={revenue}
                        endAdornment={<InputAdornment position="end"><span>円･m<sup>-3</sup></span></InputAdornment>}
                        margin="dense"
                        onChange={handleRevenueInput}
                        onBlur={handleRevenueBlur}
                        inputProps={{
                          step: 100,
                          min: 3000,
                          max: 12000,
                          type: 'number',
                          'aria-labelledby': 'planting-slider',
                        }}
                      />
                    </Grid>
                  </Grid>
                </WideTooltip>    </>
  );
}

export default SliderRevenue;
