//
// stage-2 の Reducer セット
//
import SDMDiagram from './sdm-core';
import LEV from './lev-core';

// store の初期値設定
const initialState = {
  sp: '',                      // 樹種
  id: '',                      // 密度管理図パラメータ参照ID
  sdm: new SDMDiagram(0),       // 密度管理オブジェクト
  height: 10,                 // 樹高 （m）
  planting: 2500,             // 植栽苗木本数 （本／ha）
  dv: '',                   // 材積・密度出力値（m3／ha, 本／ha）
  optPlanting: [null],
  optVolume: [null],
  revenue: 4000,         // 伐出純益 （円／m3／ha）
  priceSapling: 100,     // 苗木コスト （円／本）
  costManagement: 200,   // 造・育林コスト （百万円／ha）
  lev: [null],
  mai: 10.0,             // 平均年成長量 （m3／ha／年）
  discountingRate: 1.0,  // 割引率（実質金利） （ % ）
  processYield: 0.8      // 丸太製材歩留
}
// Reducer
const stageTwo = (state = initialState, action) =>{
  //Reducer 本体
  switch (action.type) {
    // 密度管理図パラメータID設定
    case 'SET_SP':{
      return {
        ...state,
        sp: action.payload,
      };
    } 
    // 密度管理図パラメータID設定
    case 'SET_ID':{
      return {
        ...state,
        id: action.payload,
        sdm: new SDMDiagram(action.payload),
        dv: state.sdm.PH2DV2(state.height, state.planting),
        optPlanting:  Math.floor(state.sdm.OptimalPlanting(state.height, state.revenue * state.processYield, state.priceSapling, state.costManagement * 1e4)),
        optVolume: state.sdm.PH2DV2(state.height, state.optPlanting).volume,
        lev: new LEV(state.revenue, state.costManagement * 1e4, state.optPlanting * state.priceSapling, state.processYield, state.discountingRate),
      };
    } 
    // 樹高設定
    case 'SET_HEIGHT':{
      return {
        ...state,
        height: action.payload,
        dv: state.sdm == null ? null : state.sdm.PH2DV2(action.payload, state.planting),
        optPlanting: state.sdm == null ? null : Math.floor(state.sdm.OptimalPlanting(action.payload, state.revenue * state.processYield, state.priceSapling, state.costManagement * 1e4)),
        optVolume: state.sdm == null ? null : state.sdm.PH2DV2(action.payload, state.optPlanting).volume,
        lev: new LEV(state.revenue, state.costManagement * 1e4, state.optPlanting * state.priceSapling, state.processYield, state.discountingRate),
      };
    }
    // 植栽本数設定
    case 'SET_PLANTING':{
      return {
        ...state,
        planting: action.payload,
        dv: state.sdm == null ? null : state.sdm.PH2DV2(state.height, action.payload),
      };
    }
    // 密度・材積セット更新
    case 'SET_DV':{
      return {
        ...state,
        dv: state.sdm == null ? null : state.sdm.PH2DV2(state.height, state.planting),
      };
    }
    // 伐出純益設定
    case 'SET_REVENUE':{
      return {
        ...state,
        revenue: action.payload,
        optPlanting:  Math.floor(state.sdm.OptimalPlanting(state.height, action.payload * state.processYield, state.priceSapling, state.costManagement * 1e4)),
        optVolume: state.sdm.PH2DV2(state.height, state.optPlanting).volume,
        lev: new LEV(action.payload, state.costManagement * 1e4, state.optPlanting * state.priceSapling, state.processYield, state.discountingRate),
      };
    }
    // 苗木コスト設定
    case 'SET_SAPLING_PRISE':{
      return {
        ...state,
        priceSapling: action.payload,
        optPlanting:  Math.floor(state.sdm.OptimalPlanting(state.height, state.revenue * state.processYield, action.payload, state.costManagement * 1e4)),
        optVolume: state.sdm.PH2DV2(state.height, state.optPlanting).volume,
        lev: new LEV(state.revenue, state.costManagement * 1e4, state.optPlanting * action.payload, state.processYield, state.discountingRate),
      };
    }
    // 造・育林コスト設定
    case 'SET_MANAGEMENT_COST':{
      return {
        ...state,
        costManagement: action.payload,
        optPlanting:  Math.floor(state.sdm.OptimalPlanting(state.height, state.revenue * state.processYield, state.priceSapling, action.payload * 1e4)),
        optVolume: state.sdm.PH2DV2(state.height, state.optPlanting).volume,
        lev: new LEV(state.revenue, action.payload * 1e4, state.optPlanting * state.priceSapling, state.processYield, state.discountingRate),
      };
    }
    // MAI設定
    case 'SET_MAI':{
      return {
        ...state,
        mai: action.payload,
      };
    }
    // 割引率設定
    case 'SET_DISCOUNTING_RATE':{
      return {
        ...state,
        discountingRate: action.payload,
        optPlanting:  Math.floor(state.sdm.OptimalPlanting(state.height, state.revenue * state.processYield, state.priceSapling, state.costManagement * 1e4)),
        optVolume: state.sdm.PH2DV2(state.height, state.optPlanting).volume,
        lev: new LEV(state.revenue, state.costManagement * 1e4, state.optPlanting * state.priceSapling, state.processYield, action.payload),
      };
    }
    // 丸太歩留設定
    case 'SET_PROCESS_YIELD':{
      return {
        ...state,
        processYield: action.payload,
        lev: new LEV(state.revenue, state.costManagement * 1e4, state.optPlanting * state.priceSapling, action.payload, state.discountingRate),
      };
    }
    default:
      return state;
  }
};
// reducer ここまで

export default stageTwo;