// LEV概要カード
import Typography from '@mui/material/Typography';
import Box from '@mui/system/Box';
import Card from '@mui/material/Card';
import CardHeader from '@mui/material/CardHeader';
import CardContent from '@mui/material/CardContent';
//

// LEV計算
const CalcLEVSummary = (sdm, lev, optP, optV, mai, priceSapling, costMgmt) => {
  const thresholdYear = lev.threshold(optV);
  const thresholdMAI = isNaN(thresholdYear) ? NaN : optV / thresholdYear;
  const revenueForestry = lev.fc(optV) * 1e-4 - (optP * priceSapling * 1e-4 + costMgmt);
  const npvOutput = lev.npv(optV, optV / mai);
  const levOutput = lev.lev(optV, optV / mai);

  return (
    <Card
      id="LEV-summary"
      sx={{
        width: 560,
        boxShadow: 3,
      }}
    >
      <CardHeader title="LEVに基づく収益判断" />
      <CardContent
        sx={{
          m: 0,
        }}
      >
        <Typography l={1} component="div">
          <Box textAlign="left" m={2}>
            ・林業純益:
            <font size="+1"> {Math.round(revenueForestry)} </font> 万円
          </Box>
          <Box textAlign="left" m={2}>
            ・LEVがプラスになる平均年成長量 (MAI):
            <font size="+1"> {Math.ceil(thresholdMAI)} </font> m<sup>3</sup>ha<sup>-1</sup>年<sup>-1</sup> 以上
          </Box>
          {/*
            mai > thresholdMAI ? */}
              <>
                <Box textAlign="left" m={2}>
                  ・MAIが {mai}m<sup>3</sup>ha<sup>-1</sup>年<sup>-1</sup>の時の伐期:
                  <font size="+1"> {Math.ceil(optV / mai)} </font> 年
                </Box>
                <Box textAlign="left" m={2}>
                  ・正味現在価値（NPV）:
                  <font size="+1"> {Math.round(npvOutput / 10000)} </font> 万円
                </Box>
                <Box textAlign="left" m={2}>
                  ・土地希望価値（LEV）:
                  <font size="+1"> {Math.round(levOutput / 10000)} </font> 万円
                </Box>
              </>
            {/*  : ""
          */}
        </Typography>
      </CardContent>
    </Card>
  );
};

export default CalcLEVSummary;
