/*
// 目的樹高設定時に収支最大となる植栽本数を求めるようにする
*/
import React from 'react';
//
import { scaleLinear, scaleOrdinal } from '@visx/scale';
import { AxisLeft, AxisBottom } from '@visx/axis';
import { GridRows, GridColumns } from '@visx/grid';
import { Group } from '@visx/group';
import { curveBasis } from '@visx/curve';
import { LinePath } from '@visx/shape';
import { Annotation, Label, Connector, LineSubject } from '@visx/annotation';
import { LegendOrdinal } from '@visx/legend';
//
import { Card, CardContent } from '@mui/material';
import { positions } from '@mui/system';
//

function OptimalPlanting(sdm, lev, height, priceSapling, costManagement, optP, optV) {
  // x軸範囲設定
  const xTop = 6000;
  const xBottom = 500;

  // 植栽本数配列
  const planting = new Array(56).fill(null).map((_, i) => (i) * 100 + 500);

  // 純収益（材単価 - 立米あたりの伐出コスト。いずれ スライダーに）
  // const revenueNet = 5000;
  // 苗木価格 （これもいずれスライダーに）
  // const priceSapling = 100;
  // 造・育林コスト（これは固定値か？）
  // const costManagement = 2e6;

  // 植栽本数・目標樹高から、材積を計算
  const volume = planting.flatMap(p => (sdm.PH2DV2(height, p).volume));

  // y軸範囲設定
  const yTop = 500;
  const yBottom = 0;

  // scales
  const plantingScale = scaleLinear({
    domain: [xBottom, xTop],
    clamp: true,
  });
  const balanceScale = scaleLinear({
    domain: [yBottom, yTop],
    clamp: true,
  });
  const legendScale = scaleOrdinal({
    domain: ["伐出純益", "植栽・育林コスト"],
    range: ["#282", "#A22"],
  });
  // graph margin
  const defaultMargin = { top: 30, right: 30, bottom: 70, left: 70 };


  // 主線描画データ作成
  const balanceData = planting.map((p, i) => ({
    planting: p,
    cost: p * priceSapling / 10000 + costManagement,
    revenue: Math.round(lev.fc(volume[i]) / 1000) / 10,
  }));

  // 最適植栽本数描画データ作成
  const optPlantingData = [
    { planting: optP, back: 0, balance: optP * priceSapling / 10000 + costManagement },
    { planting: optP, back: 500, balance: Math.round(lev.fc(optV) / 1000) / 10 },
  ]



  // グラフ描画設定
  const svgWidth = 640;
  const svgHeight = 480;
  const background = '#f8f8f8'; // うっすら灰色
  const margin = defaultMargin;
  const xMax = svgWidth - margin.left - margin.right;
  const yMax = svgHeight - margin.top - margin.bottom;
  plantingScale.range([0, xMax]);
  balanceScale.range([yMax, 0]);

  return (
    <>
      <svg width={svgWidth} height={svgHeight}>
        {/* グラフ描画ボックス */}

        <rect x={0} y={0} width={svgWidth} height={svgHeight} fill={background} rx={14} />
        <Group left={margin.left} top={margin.top}>
          {/* 軸描画 */}
          <GridRows scale={balanceScale} width={xMax} height={yMax} stroke="#e0e0e0" />
          <GridColumns scale={plantingScale} width={xMax} height={yMax} stroke="#e0e0e0" />
          <line x1={xMax} x2={xMax} y1={0} y2={yMax} stroke="#e0e0e0" />
          <AxisBottom
            top={yMax}
            scale={plantingScale}
            label={"植栽本数（本／ha）"}
            labelProps={{ fontSize: 16, textAnchor: "middle", }}
          />
          <AxisLeft
            scale={balanceScale}
            numTicks={5}
            label={"金額（万円）"}
            labelProps={{ fontSize: 16, textAnchor: "middle", }}
          />
          {/* 材収益 */}
          <LinePath
            data={balanceData}
            curve={curveBasis}
            x={(d) => plantingScale(d.planting) ?? 0}
            y={(d) => balanceScale(d.revenue) ?? 0}
            stroke="#282"
            strokeWidth={1.5}
            strokeOpacity={0.8}
          />
          {/* 苗木コスト */}
          <LinePath
            data={balanceData}
            curve={curveBasis}
            x={(d) => plantingScale(d.planting) ?? 0}
            y={(d) => balanceScale(d.cost) ?? 0}
            stroke="#A02020"
            strokeWidth={1.5}
            strokeOpacity={1}
          />
          {/* 最適植栽本数ライン */}
          <LinePath
            data={optPlantingData}
            curve={curveBasis}
            x={(d) => plantingScale(d.planting) ?? 0}
            y={(d) => balanceScale(d.balance) ?? 0}
            stroke={optPlantingData[0].balance >= optPlantingData[1].balance ? "" : "#FA2"}
            strokeWidth={4}
            strokeOpacity={1}
          />
          {/* 注釈-収益 */}
          {optPlantingData[1].balance > optPlantingData[0].balance
            ?
            <Annotation
              x={plantingScale(optPlantingData[1].planting) ?? 0}
              y={balanceScale(optPlantingData[1].balance) ?? 0}
              dx={60}
              dy={balanceScale(optPlantingData[1].balance) - 60 > 0 ? -30 : 80 - balanceScale(optPlantingData[1].balance)}
            >
              <Connector stroke={"#ff7e67"} type={"elbow"} />
              <Label
                backgroundFill="white"
                showAnchorLine={"true"}
                anchorLineStroke={"#228b22"}
                backgroundProps={{ stroke: "#ecf4f3" }}
                fontColor={"#2f4f4f"}
                horizontalAnchor={"start"}
                verticalAnchor={plantingScale(optP) + 165 > 0 ? "middle" : "top"}
                title={"収益"}
                titleFontSize={12}
                subtitle={
                  Math.round(optPlantingData[1].balance) + " 万円  "
                  + "(" + Math.round(optV) + " m3)"
                }
                subtitleFontSize={16}
                subtitleDy={2}
                subtitleProps={{ align: "center" }}
                Maxwidth={120}
              />
            </Annotation>
            : ""
          }
          {/* 注釈-造林コスト */}
          <Annotation
            x={plantingScale(optPlantingData[0].planting) ?? 0}
            y={balanceScale(optPlantingData[0].balance) ?? 0}
            dx={plantingScale(optP) - 165 > 0 ? -60 : 60}
            dy={balanceScale(optPlantingData[0].balance) + 80 < 340 ? 30 : 260 - balanceScale(optPlantingData[0].balance)}
          >
            <Connector stroke={"#ff7e67"} type={"elbow"} />
            <Label
              backgroundFill="white"
              showAnchorLine={"true"}
              anchorLineStroke={"#dc143c"}
              backgroundProps={{ stroke: "#ecf4f3" }}
              fontColor={"#800000"}
              horizontalAnchor={plantingScale(optP) - 165 > 0 ? "end" : "start"}
              verticalAnchor={"middle"}
              title={"造・育林コスト"}
              titleFontSize={12}
              subtitle={Math.round(optPlantingData[0].balance) + "万円"}
              subtitleFontSize={16}
              subtitleDy={2}
              subtitleProps={{ align: "center" }}
              width={100}
            />
          </Annotation>
          {/* 注釈-最適植栽数 */}
          <Annotation
            x={plantingScale(optP) ?? 0}
            y={balanceScale(0) ?? 0}
            dx={60}
            dy={-40}
          >
            <Connector stroke={"#ff7e67"} type={"elbow"} />
            <Label
              backgroundFill="white"
              showAnchorLine={"true"}
              anchorLineStroke={"#214599"}
              backgroundProps={{ stroke: "#ecf4f3" }}
              fontColor={"#133299"}
              horizontalAnchor={"start"}
              title={"最適植栽本数"}
              titleFontSize={12}
              subtitle={optP + "本"}
              subtitleFontSize={16}
              subtitleDy={2}
              subtitleProps={{ align: "center" }}
              verticalAnchor={"middle"}
              width={100}
            />
            <LineSubject
              orientation={"vertical"}
              stroke={"#22A"}
              min={balanceScale(yBottom)}
              max={balanceScale(yTop)}
            />
          </Annotation>
        </Group>
      </svg>
      {/* 凡例 */}
      <Card
        variant="outlined"
        sx={{
          width: 150,
          height: 40,
          position: 'relative',
          top: -475,
          left: 500,
        }}
      >
        <CardContent
          sx={{
            fontSize: 12,
            align: "center",
            lineHeight: 1.5,
            ml: 1,
            mr: 1,
            mt: 0,
            p: 0,
          }}
        >
          <LegendOrdinal
            scale={legendScale}
            direction="column"
            itemDirection="row"
            shape="line"
            labelMargin="0 15px 0 0"
          />
        </CardContent>
      </Card>
    </>
  );
};

export default OptimalPlanting;

