import React, {useState} from 'react';
import { useSelector, useDispatch } from 'react-redux';

// Material-UI selector （var 5）
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import Slider from '@mui/material/Slider';
import Input from '@mui/material/Input';
import InputAdornment from '@mui/material/InputAdornment';
import { styled } from '@mui/styles';
import Tooltip, { tooltipClasses } from '@mui/material/Tooltip';
//

// サイズ指定可能なtool-tipボックス
const WideTooltip = styled(({ className, ...props }) => (
  <Tooltip {...props} classes={{ popper: className }} />
))({
  [`& .${tooltipClasses.tooltip}`]: {
    maxWidth: 300,
  },
});

// コンポーネント本体
function SliderCostManagement() {
  // 各種hook設定
  const dispatch = useDispatch();
  const costManagement = useSelector(state => state.costManagement);
  const setCostManagement = (newCostManagement) => {
    dispatch({ type: 'SET_MANAGEMENT_COST', payload: newCostManagement });
  }
  const [balance, setBalance] = useState([100,300]);  // ローカルで利用する収支


  // 育林経費調整
  const textCmSlider = `
    造林・育林経費のうち、植栽本数に依存しないコストを設定。スライダーの右端で実経費額を、左端で補助金額を設定できます。
    `;
  const handleCostManagementSlider = (event, newBalance) => {
    setBalance(newBalance);  // ローカルでバランスを利用
    const newCostManagement = Math.abs(newBalance[0] - newBalance[1])
    setCostManagement(newCostManagement);
  };
  const handleCostManagementInput = (event) => {
    setCostManagement(event.target.value === '' ? NaN : Number(event.target.value));
    const newBalance = [balance[1] - Number(event.target.value), balance[1]];
    setBalance(newBalance);
  };
  const handleCostManagementBlur = () => {
    if (costManagement < 0) {
      setCostManagement(0);
    } else if (costManagement > 300) {
      setCostManagement(300);
    }
  };
  const cmMarks = [
    { value: 0, label: '0', },
    { value: 50, label: '50', },
    { value: 100, label: '100', },
    { value: 150, label: '', },
    { value: 200, label: '200', },
    { value: 250, label: '', },
    { value: 300, label: '300', },
    { value: 350, label: '', },
    { value: 400, label: '400万円', },
  ];

  // 出力
  return (
    <>
      {/*造・育林コストスライダ*/}
      <Typography id="prise-input-title">
        育林コスト
      </Typography>
      <WideTooltip title={textCmSlider} placement="top-start" arrow>
        <Grid container id="costManagement-inputs" spacing={4} alignItems="center"
          sx={{
            mb: { xs: 3 },
          }}
        >
          <Grid item id="costManagement-slider" xs>
            <Slider
              // value={typeof costManagement === 'number' ? costManagement : 200}
              value={balance}
              onChange={handleCostManagementSlider}
              getAriaValueText={(v) => { return ('{$v}万円') }}
              aria-labelledby="priceSapling-slider"
              step={10}
              marks={cmMarks}
              min={0}
              max={400}
              valueLabelDisplay="auto"
            />
          </Grid>
          <Grid item id="costManagement-number" >
            <Input
              sx={{
                backgroundColor: "#ffffff",
                ml: { xs: 1 },
                width: 120,
              }}
              value={costManagement}
              endAdornment={<InputAdornment position="end"><span>万円･ha<sup>-1</sup></span></InputAdornment>}
              margin="dense"
              onChange={handleCostManagementInput}
              onBlur={handleCostManagementBlur}
              inputProps={{
                step: 10,
                min: 0,
                max: 300,
                type: 'number',
                'aria-labelledby': 'costManagement-slider',
              }}
            />
          </Grid>
        </Grid>
      </WideTooltip>
    </>
  );
}

export default SliderCostManagement;
