
import React from 'react';
import { useSelector, useDispatch } from 'react-redux';

// Material-UI selector （var 5）
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import Slider from '@mui/material/Slider';
import Input from '@mui/material/Input';
import InputAdornment from '@mui/material/InputAdornment';
import { styled } from '@mui/styles';
import Tooltip, { tooltipClasses } from '@mui/material/Tooltip';
//

// サイズ指定可能なtool-tipボックス
const WideTooltip = styled(({ className, ...props }) => (
  <Tooltip {...props} classes={{ popper: className }} />
))({
  [`& .${tooltipClasses.tooltip}`]: {
    maxWidth: 300,
  },
});

// コンポーネント本体
function SliderMAI() {
  // 各種hook設定
  const dispatch = useDispatch();
  const mai = useSelector(state => state.mai);
  const setMAI = (newMAI) => {
    dispatch({ type: 'SET_MAI', payload: newMAI });
  }

  // 平均成長量調整
  const textMAISlider = `
    LEVによる収益判断のための平均年成長量を設定。この値が算出されるMAI閾値を上回る場合、LEVがプラスとなる伐期が結果欄に表示されます。
    `;
  const handleMAISlider = (event, newMAI) => {
    setMAI(newMAI);
  };
  const handleMAIInput = (event) => {
    setMAI(event.target.value === '' ? NaN : Number(event.target.value));
  };
  const handleMAIBlur = () => {
    if (mai < 5.0) {
      setMAI(5.0);
    } else if (mai > 25.0) {
      setMAI(25.0);
    }
  };
  const maiMarks = [
    { value: 5, label: '5', },
    { value: 10, label: '10', },
    { value: 15, label: '', },
    { value: 20, label: '20m3/ha/年', },
    { value: 25, label: '', },
  ];

  // 出力
  return (
    <>
      {/*MAIスライダ*/}
      <Typography id="prise-input-title">
        年平均成長量（MAI）
      </Typography>
      <WideTooltip title={textMAISlider} placement="top-start" arrow>
        <Grid container id="MAI-inputs" spacing={4} alignItems="center"
          sx={{
            mb: { xs: 3 },
          }}
        >
          <Grid item id="MAI-slider" xs>
            <Slider
              value={typeof mai === 'number' ? mai : 10}
              onChange={handleMAISlider}
              getAriaValueText={(v) => { return ('{$v}m3/ha/y') }}
              aria-labelledby="priceSapling-slider"
              step={1}
              marks={maiMarks}
              min={5}
              max={25}
              valueLabelDisplay="auto"
            />
          </Grid>
          <Grid item id="MAI-number" >
            <Input
              sx={{
                backgroundColor: "#ffffff",
                ml: { xs: 1 },
                width: 120,
              }}
              value={mai}
              endAdornment={<InputAdornment position="end"><span>m<sup>3</sup>ha<sup>-1</sup>年<sup>-1</sup></span></InputAdornment>}
              margin="dense"
              onChange={handleMAIInput}
              onBlur={handleMAIBlur}
              inputProps={{
                step: 1,
                min: 5,
                max: 25,
                type: 'number',
                'aria-labelledby': 'costMgmt-slider',
              }}
            />
          </Grid>
        </Grid>
      </WideTooltip>

    </>
  );
}

export default SliderMAI;
