//
// stage-2 用の パラメータIDセレクタコンポーネント
// このコンポーネントは sp 選択枝が sp で場合分けされる。
//

import React from 'react';
import { useSelector, useDispatch } from 'react-redux';

// Material-UI selector （var 5）
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import { styled } from '@mui/styles';
import Tooltip, { tooltipClasses } from '@mui/material/Tooltip';
//

// サイズ指定可能なtool-tipボックス
const WideTooltip = styled(({ className, ...props }) => (
  <Tooltip {...props} classes={{ popper: className }} />
))({
  [`& .${tooltipClasses.tooltip}`]: {
    maxWidth: 300,
  },
});

// コンポーネント本体
function SelectorID() {
  // 各種hook設定
  const dispatch = useDispatch();
  const id = useSelector(state => state.id);
  const setID = (newID) => {
    dispatch({ type: 'SET_ID', payload: newID });
    dispatch({ type: 'SET_DV'});
  }
  const sp = useSelector(state => state.sp);

  // エリア変更
  const textChangeArea = `
    密度管理図の対象とする地域を選択
    `;
  const handleChangeID = (event) => {
    setID(event.target.value);
  };

  // 西園ら (2013) に基づく密度管理図パラメータリスト
  const parameters = require('./parameters.json');
  const selectedParameters = parameters.filter(d => d.spID == sp);

  // 出力
  return (
    <>
      {/*エリア選択*/}
      <WideTooltip title={textChangeArea} placement="right-end" arrow>
        <FormControl
          sx={{
            margin: { xs: 1 },
            minWidth: 120,
            padding: { xs: 1 },
          }}
        >
          <InputLabel id="area-select-label">エリア</InputLabel>
          <Select
            sx={{
              backgroundColor: "#ffffff",
            }}
            labelId="area-select-label"
            id="area-select"
            value={id}
            onChange={handleChangeID}
          >
            {selectedParameters.map(v => { return (<MenuItem value={v.ID}>{v.area}</MenuItem>) })}
          </Select>                </FormControl>
      </WideTooltip>
    </>
  );
}

export default SelectorID;