//
// stage-2 用の 割引率（実質金利）セレクタコンポーネント
//

import React from 'react';
import { useSelector, useDispatch } from 'react-redux';

// Material-UI selector （var 5）
import Box from '@mui/material/Box';
import InputLabel from '@mui/material/InputLabel';
import OutlinedInput from '@mui/material/OutlinedInput';
import InputAdornment from '@mui/material/InputAdornment';
import { styled } from '@mui/styles';
import Tooltip, { tooltipClasses } from '@mui/material/Tooltip';
//

// サイズ指定可能なtool-tipボックス
const WideTooltip = styled(({ className, ...props }) => (
  <Tooltip {...props} classes={{ popper: className }} />
))({
  [`& .${tooltipClasses.tooltip}`]: {
    maxWidth: 300,
  },
});

// コンポーネント本体
function SelectorDR() {
  // 各種hook設定
  const dispatch = useDispatch();
  const discountingRate = useSelector(state => state.discountingRate);
  const setDiscountingRate = (newDR) => {
    dispatch({ type: 'SET_DISCOUNTING_RATE', payload: newDR });
  }

  // 割引率調整
  const textDRInput = `
    LEVによる収益判断のための割引率を設定。割引率が0%の場合は、林業純益 ＝ 正味現在価値となります。
    `;
  const handleDiscountingRateInput = (event) => {
    setDiscountingRate(event.target.value === '' ? NaN : Number(event.target.value));
  };
  const handleDiscountingRateBlur = () => {
    if (discountingRate < 0) {
      setDiscountingRate(0);
    } else if (discountingRate > 2) {
      setDiscountingRate(2);
    }
  };


  // 出力
  return (
    <Box sx={{mx:{xs:6},}}>
      {/*割引率選択*/}
      <InputLabel htmlFor="outlined-adornment-amount">割引率</InputLabel>
      <WideTooltip title={textDRInput} placement="top" arrow>
        <OutlinedInput
          sx={{
            backgroundColor: "#ffffff",
            width: 108,
          }}
          value={discountingRate}
          endAdornment={<InputAdornment position="end">%</InputAdornment>}
          onChange={handleDiscountingRateInput}
          onBlur={handleDiscountingRateBlur}
          inputProps={{
            step: 0.1,
            min: 0.0,
            max: 2.0,
            type: 'number',
            'aria-labelledby': 'discountingRate',
          }}
        />
      </WideTooltip>
    </Box>
  );
}

export default SelectorDR;
