/* 
  Land Expectation Value 推定用のクラス

*/


class LEV{
  // 初期値
  constructor(rPN, cM, cS, pY, dR){
    this.revenueProductionNet = rPN ?? 3500;  // 材販売純益（販売コストは先に減じておく）
    this.costManagement = cM ?? 2e6;           // 育林コスト
    this.costSapling = cS ?? 100*2500;        // 苗木コスト
    this.processYield = pY ?? 0.8;            // 歩留まり
    this.discountingRate = dR ?? 1;           // 値引率（実質金利）%で
  };

  //パラメータ設定
  setParms (rPN, cM, cS, pY, dR){
    this.revenueProductionNet = rPN;
    this.costManagement = cM;
    this.costSapling = cS;
    this.processYield = pY;
    this.discountingRate = dR;
  };

  // Expectation Value の計算
  fc (volume){
    return(
      volume*this.processYield*this.revenueProductionNet
    );
  };

  // NPV 計算
  npv (volume, year) {
    return(
      this.fc(volume)/(1+this.discountingRate/100)**year - (this.costManagement + this.costSapling)
    );
  };

  // LEV 計算
  lev (volume, year){
    return(
      this.npv(volume, year) / (1 - (1/(1 + this.discountingRate/100))**year) 
      );
  };

  // threshold 計算
  threshold (volume) {
    const resultY =  Math.log((this.costManagement + this.costSapling)/this.fc(volume))/(-Math.log(1+this.discountingRate/100));
    return(resultY > 0  ? Math.floor(resultY) : NaN);
  } 

};

export default LEV;