
import React from 'react';
import {useSelector, useDispatch} from 'react-redux';

// Material-UI selector （var 5）
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import Slider from '@mui/material/Slider';
import Input from '@mui/material/Input';
import InputAdornment from '@mui/material/InputAdornment';
import { styled } from '@mui/styles';
import Tooltip, { tooltipClasses } from '@mui/material/Tooltip';
//

// サイズ指定可能なtool-tipボックス
const WideTooltip = styled(({ className, ...props }) => (
    <Tooltip {...props} classes={{ popper: className }} />
  ))({
    [`& .${tooltipClasses.tooltip}`]: {
      maxWidth: 300,
    },
  });

// コンポーネント本体
function SliderHeight() {
  // 各種hook設定
  const dispatch = useDispatch();
  const height = useSelector(state => state.height);
  const setHeight = (newHeight) =>{
    dispatch({type: 'SET_HEIGHT', payload: newHeight});
  }

  // 目標樹高調整ハンドラ
  const textHeightSlider = `
    目標とする樹高を設定
    `;
  const handleHeightSlider = (event, inputHeight) => {
    setHeight(inputHeight)
  };
  const handleHeightInput = (event) => {
    setHeight(event.target.value === '' ? NaN : Number(event.target.value));
  };
  const handleHeightBlur = () => {
    if (height < 10) {
      setHeight(10);
    } else if (height > 60) {
      setHeight(60);
    }
  };
  const hMarks = [
    { value: 10, label: '10m', },
    { value: 20, label: '20m', },
    { value: 50, label: '50m', },
  ];

  // 出力
  return (
    <>
      {/*樹高スライダー*/}
      <Typography id="height-input-tile">
        樹高
      </Typography>
      <WideTooltip title={textHeightSlider} placement="bottom" arrow>
        <Grid container id="height-inputs" spacing={4} alignItems="center"
          sx={{
            mb: { xs: 3 },
          }}
        >
          <Grid item id="height-slider" xs>
            <Slider
              value={typeof height === 'number' ? height : 10}
              onChange={handleHeightSlider}
              getAriaValueText={(v) => { return ('{$v}m') }}
              aria-labelledby="height-inputs"
              min={10}
              max={50}
              valueLabelDisplay="auto"
              marks={hMarks}
            />
          </Grid>
          <Grid item id="height-number" >
            <Input
              sx={{
                backgroundColor: "#ffffff",
                ml: { xs: 2 },
                minWidth: 81,
              }}
              value={height}
              endAdornment={<InputAdornment position="end">m</InputAdornment>}
              margin="dense"
              onChange={handleHeightInput}
              onBlur={handleHeightBlur}
              inputProps={{
                step: 1,
                min: 10,
                max: 50,
                type: 'number',
                'aria-labelledby': 'height-inputs',
              }}
            />
          </Grid>
        </Grid>
      </WideTooltip>
    </>
  );
}

export default SliderHeight;
