//
// stage-2 用の 樹種セレクタコンポーネント
// sp は id に内包されるため、直接的な機能は無いが、
// id 選択の場合分けに必要
//


import React from 'react';
import { useSelector, useDispatch } from 'react-redux';

// Material-UI selector （var 5）
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import { styled } from '@mui/styles';
import Tooltip, { tooltipClasses } from '@mui/material/Tooltip';
//

// サイズ指定可能なtool-tipボックス
const WideTooltip = styled(({ className, ...props }) => (
  <Tooltip {...props} classes={{ popper: className }} />
))({
  [`& .${tooltipClasses.tooltip}`]: {
    maxWidth: 300,
  },
});

// コンポーネント本体
function SelectorSp() {
  // 各種hook設定
  const dispatch = useDispatch();
  const sp = useSelector(state => state.sp);
  const setSp = (newSp) => {
    dispatch({ type: 'SET_SP', payload: newSp });
  }

  //樹種変更
  const textChangeSP = `
    密度管理図の対象とする樹種を選択
    `;
  const handleChangeSp = (event) => {
    setSp(event.target.value);
  };


  // 出力
  return (
    <>
      {/*樹種選択*/}
      <WideTooltip title={textChangeSP} placement="right-end" arrow>
        <FormControl
          sx={{
            margin: { xs: 1 },
            minWidth: 120,
            padding: { xs: 1 },
          }}
        >
          <InputLabel id="sp-select-label">樹種</InputLabel>
          <Select
            sx={{
              backgroundColor: "#ffffff",
            }}
            labelId="sp-select-label"
            id="sp-select"
            value={sp}
            onChange={handleChangeSp}
          >
            <MenuItem value={0}>スギ</MenuItem>
            <MenuItem value={1}>ヒノキ</MenuItem>
            <MenuItem value={2}>カラマツ</MenuItem>
            <MenuItem value={3}>アカマツ</MenuItem>
            <MenuItem value={4}>針葉樹</MenuItem>
            <MenuItem value={5}>広葉樹</MenuItem>
          </Select>
        </FormControl>
      </WideTooltip>
    </>
  );
}

export default SelectorSp;
