
import React from 'react';
import { useSelector, useDispatch } from 'react-redux';

// Material-UI selector （var 5）
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import Slider from '@mui/material/Slider';
import Input from '@mui/material/Input';
import InputAdornment from '@mui/material/InputAdornment';
import { styled } from '@mui/styles';
import Tooltip, { tooltipClasses } from '@mui/material/Tooltip';
//

// サイズ指定可能なtool-tipボックス
const WideTooltip = styled(({ className, ...props }) => (
  <Tooltip {...props} classes={{ popper: className }} />
))({
  [`& .${tooltipClasses.tooltip}`]: {
    maxWidth: 300,
  },
});

// コンポーネント本体
function SliderPlanting() {
  // 各種hook設定
  const dispatch = useDispatch();
  const planting = useSelector(state => state.planting);
  const setPlanting = (newPlanting) => {
    dispatch({ type: 'SET_PLANTING', payload: newPlanting });
  }

  // 目標樹高調整ハンドラ
  // 植栽本数調整
  const textPlantingSlider = `
    植栽本数を設定。間伐による個体数調整もこの値を変化させることで擬似的に可能です。
    `;
  const handlePlantingSlider = (event, inputPlanting) => {
    setPlanting(inputPlanting);
  };
  const handlePlantingInput = (event) => {
    setPlanting(event.target.value === '' ? NaN : Number(event.target.value));
  };
  const handlePlantingBlur = () => {
    if (planting < 100) {
      setPlanting(100);
    } else if (planting > 5000) {
      setPlanting(5000);
    }
  };
  const dMarks = [
    { value: 500, label: '500本', },
    { value: 1000, },
    { value: 1500, },
    { value: 2000, },
    { value: 2500, },
    { value: 3000, },
    { value: 3500, },
    { value: 4000, },
    { value: 4500, },
    { value: 5000, label: '5000本', },
  ];

  // 出力
  return (
    <>
      {/*苗木本数スライダー*/}
      <Typography id="planting-input-title" gutterBottom
        sx={{
          mt: { xs: 2 },
          ml: { xs: 1 },
        }}
      >
        植栽本数
      </Typography>
      <WideTooltip title={textPlantingSlider} placement="bottom" arrow>
        <Grid container id="planting-inputs" spacing={4} alignItems="center"
          sx={{
            mb: { xs: 3 },
          }}
        >
          <Grid item id="planting-slider" xs>
            <Slider
              value={typeof planting === 'number' ? planting : 2500}
              onChange={handlePlantingSlider}
              getAriaValueText={(v) => { return ('{$v}本') }}
              aria-labelledby="planting-inputs"
              step={500}
              //marks
              marks={dMarks}
              min={500}
              max={5000}
              valueLabelDisplay="auto"
            />
          </Grid>
          <Grid item id="planting-number" xs={3}>
            <Input
              sx={{
                backgroundColor: "#ffffff",
                ml: { xs: 2 },
                minWidth: 81,
              }}
              value={planting}
              endAdornment={<InputAdornment position="end">本</InputAdornment>}
              margin="dense"
              onChange={handlePlantingInput}
              onBlur={handlePlantingBlur}
              inputProps={{
                step: 100,
                min: 100,
                max: 5000,
                type: 'number',
                'aria-labelledby': 'planting-inputs',
              }}
            />
          </Grid>
        </Grid>
      </WideTooltip>
    </>
  );
}

export default SliderPlanting;
