
import React from 'react';
import { useSelector, useDispatch } from 'react-redux';

// Material-UI selector （var 5）
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import Slider from '@mui/material/Slider';
import Input from '@mui/material/Input';
import InputAdornment from '@mui/material/InputAdornment';
import { styled } from '@mui/styles';
import Tooltip, { tooltipClasses } from '@mui/material/Tooltip';
//

// サイズ指定可能なtool-tipボックス
const WideTooltip = styled(({ className, ...props }) => (
  <Tooltip {...props} classes={{ popper: className }} />
))({
  [`& .${tooltipClasses.tooltip}`]: {
    maxWidth: 300,
  },
});

// コンポーネント本体
function SliderPriceSapling() {
  // 各種hook設定
  const dispatch = useDispatch();
  const priceSapling = useSelector(state => state.priceSapling);
  const setPriceSapling = (newPriceSapling) => {
    dispatch({ type: 'SET_SAPLING_PRISE', payload: newPriceSapling });
  }

  // 苗木価格調整
  const textPsSlider = `
    苗木コストを設定。苗木単価に加えて、基肥・単木保護資材コストなどを考慮しても良いかも知れません。スライダーは50～200円の範囲ですが、入力窓は最高3000円まで設定できます。
    `;
  const handlePriceSaplingSlider = (event, newPriceSapling) => {
    setPriceSapling(newPriceSapling);
  };
  const handlePriceSaplingInput = (event) => {
    setPriceSapling(event.target.value === '' ? NaN : Number(event.target.value));
  };
  const handlePriceSaplingBlur = () => {
    if (priceSapling < 50) {
      setPriceSapling(50);
    } else if (priceSapling > 3000) {
      setPriceSapling(3000);
    }
  };
  const csMarks = [
    { value: 50, label: '50', },
    { value: 100, label: '100', },
    { value: 150, label: '150', },
    { value: 200, label: '200円', },
  ];

  // 出力
  return (
    <>
      {/*苗木コストスライダ*/}
      <Typography id="price-input-title">
        苗木価格
      </Typography>
      <WideTooltip title={textPsSlider} placement="top-start" arrow>
        <Grid container id="price-inputs" spacing={4} alignItems="center"
          sx={{
            mb: { xs: 3 },
          }}
        >
          <Grid item id="price-slider" xs>
            <Slider
              value={typeof priceSapling === 'number' ? priceSapling : 100}
              onChange={handlePriceSaplingSlider}
              getAriaValueText={(v) => { return ('{$v}円') }}
              aria-labelledby="priceSapling-slider"
              step={10}
              marks={csMarks}
              min={50}
              max={200}
              valueLabelDisplay="auto"
            />
          </Grid>
          <Grid item id="price-number" >
            <Input
              sx={{
                backgroundColor: "#ffffff",
                ml: { xs: 1 },
                width: 120,
              }}
              endAdornment={<InputAdornment position="end"><span>円･本<sup>-1</sup></span></InputAdornment>}
              value={priceSapling}
              margin="dense"
              onChange={handlePriceSaplingInput}
              onBlur={handlePriceSaplingBlur}
              inputProps={{
                step: 10,
                min: 50,
                max: 3000,
                type: 'number',
                'aria-labelledby': 'priceSapling-slider',
              }}
            />
          </Grid>
        </Grid>
      </WideTooltip>
    </>
  );
}

export default SliderPriceSapling;
